import React, { Component } from 'react';
import './css/header.css';
import search from '../../js/search';
import Buscador from '../assets/Buscador';
import Icon from '../assets/Icon';
import Information from './Information';
import AddMoney from './AddMoney';
import Wallet from './Wallet';
import Expenses from './Expenses';
import ShoppingCart from './ShoppingCart';
import Help from './Help';

class Header extends Component{

  state = {
    money: 0,
    mq: false,
    currentWindow: undefined
  }

  componentDidMount() {
    this.setSeeker()
    window.addEventListener('resize', this.setSeeker)
  }

  setSeeker = () =>{
    if (window.innerWidth >= 1024){
      this.setState({ mq: true })
    } else {
      this.setState({ mq: false })
    }
  }

  closeInformation = () =>{
    this.setState({
      currentWindow: undefined
    })
  }

  addMoney = newMoney =>{
    let condition = /^[0]|[+|-|.]|[.]$/g;
    if (document.getElementById('add-money').value === '' || condition.test(document.getElementById('add-money').value)){
      return null;
    } else {
      this.setState({
        money: this.state.money + Number(newMoney)
      })
    }
  }

  render(){
    return(
      <header>
        <div className="left-side" title="">
          <img src={`${process.env.PUBLIC_URL}/img/box.png`} alt="box"/><span>Shop</span>Lit
        </div>

        <div className="right-side">
          {this.state.mq ? <Buscador onSubmit={
            e => {
              e.preventDefault();
              this.props.onSearch(search());
              this.props.changeTextNotFound("No se ha encontrado ningún producto...");
            }
          } /> : null}

          <span onClick={ () => this.setState({currentWindow: 1})}>
          <Icon iconName="fas fa-money-check" iconTitle="Añadir dinero a tu cartera" />
            {this.state.currentWindow === 1 ? <Icon iconName="fas fa-caret-up" iconStyle={{padding: "0", border: "none"}} id="arrow-top" /> : null}
          </span>

          <span onClick={ () => this.setState({currentWindow: 2})}>
          <Icon iconName="fas fa-wallet" iconTitle="Tu cartera" />
            {this.state.currentWindow === 2 ? <Icon iconName="fas fa-caret-up" iconStyle={{padding: "0", border: "none"}} id="arrow-top" /> : null}
          </span>

          <span onClick={ () => this.setState({currentWindow: 3})}>
            <Icon iconName="fas fa-cash-register" iconTitle="Tus gastos" />
            {this.state.currentWindow === 3 ? <Icon iconName="fas fa-caret-up" iconStyle={{padding: "0", border: "none"}} id="arrow-top" /> : null}
          </span>

          <span onClick={ () => this.setState({currentWindow: 4})}>
            <Icon iconName="fas fa-cart-arrow-down" iconTitle="Carrito de compras" />
            {this.state.currentWindow === 4 ? <Icon iconName="fas fa-caret-up" iconStyle={{padding: "0", border: "none"}} id="arrow-top" /> : null}
            { this.props.productsAdded === true
            ? <span id="number-of-products-added">1</span>
            : null
            }
          </span>

          <span onClick={ () => this.setState({currentWindow: 5})}>
          <Icon iconName="fas fa-question-circle" iconTitle="Ayuda" />
            {this.state.currentWindow === 5 ? <Icon iconName="fas fa-caret-up" iconStyle={{padding: "0", border: "none"}} id="arrow-top" /> : null}
          </span>

        </div>

        { this.state.currentWindow === 5 ? <Information title="Ayuda" close={this.closeInformation} innerHTML={<Help />}/>
        : this.state.currentWindow === 4
        ? <Information title="Carrito de compras" close={this.closeInformation} innerHTML={<ShoppingCart />}/>
        : this.state.currentWindow === 3
        ? <Information title="Tus gastos" close={this.closeInformation} innerHTML={<Expenses />}/>
        : this.state.currentWindow === 2
        ? <Information title="Tu cartera" close={this.closeInformation} innerHTML={<Wallet money={this.state.money} />}/>
        : this.state.currentWindow === 1
        ? <Information title="Añade dinero a tu cartera" close={this.closeInformation} innerHTML={<AddMoney setMoney={this.addMoney}  />}/>
        : null
        }

      </header>
    )
  }
}

export default Header;