import React, { Component } from 'react';
import Icon from '../assets/Icon';
import './css/product.css';
import productsAdded from '../../js/products-added';

class Product extends Component{
  state = {
    stock: Number(this.props.stock),
    prices: Number(this.props.stock)
  }

  increase = () =>{
    if (this.state.stock !== this.state.prices){
      this.setState({
        stock: this.state.stock + 1
      })
    }
  }

  decrease = () =>{
    if (this.state.stock !== 0){
      this.setState({
        stock: this.state.stock - 1
      })
    }
  }

  minus = e =>{
    let value = e.target.parentElement.getElementsByClassName('count')[0].textContent;
    return (e.target.parentElement.getElementsByClassName('count')[0].textContent = Number(value) - 1) && (e.target.parentElement.parentElement.getElementsByClassName('count')[0].textContent = Number(value) - 1);
  }

  setPosition = e =>{
    let parent = e.target.parentElement;
    return parent.parentElement.getAttribute('data-product') || parent.getAttribute('data-product');
  }

  render(){
    const { id, imagen, modelo, precio, stock, decrease } = this.props;
    return (
      <div className="product" key={id} data-product={id}>
        <figure onClick={e => {
          document.body.style.overflow = 'hidden';
          this.props.showModal(true);
          this.props.position(this.setPosition(e));
        }}>
          <img loading="lazy" src={`${process.env.PUBLIC_URL}/${imagen}`} alt={modelo} title={modelo} />
        </figure>
        <h3>{modelo}</h3>
        <h4>$/. {precio}</h4>

        <div id="quantity">
          <button onClick={this.increase}>
            <Icon iconName="fas fa-plus" iconStyle={{padding: "0"}}/>
          </button>
          
          <div className="count">{this.state.stock}</div>
          
          <button onClick={e => {this.minus(e)}}>
            <Icon iconName="fas fa-minus" iconStyle={{padding: "0"}}/>
          </button>
        </div>

        <button className="add-to-cart" onClick={() => {
          productsAdded(this.state.stock);
          this.props.numberProducts();
          }}>
          <Icon iconName="fas fa-cart-plus" iconTitle="Añade un producto al Carrito" iconStyle={{padding: "0"}}/>&nbsp;&nbsp;Añadir al carrito</button>
      </div>
    )
  }
}

export default Product;